
.entry-content>* {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 1.25rem;
}

.entry-content>*:last-child {
  margin-bottom: 0;
}

.entry-content>*:not(.alignwide):not(.alignfull):not(.alignleft):not(.alignright):not(.is-style-wide) {
  max-width: 96rem;
  width: calc(100% - 4rem);
}

/* Video na naslovnoj strani */

.videobg {
	position:relative;
	z-index:-100;
}

video {
  object-fit: cover;
	object-position: 80% 50%;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}


/* Gutenber boje  */

.has-white-color {
	color: #ffffff;
}
.has-white-background-color {
