body,
button,
input,
select,
optgroup,
textarea {
	color: var(--bodyColor);
	font-family: var(--bodyFont);
	/* font-size: 16px; */
	line-height: 1.5;
	font-size: var(--fontSize);
	@media (--large){
		font-size: var(--fontSizeLarge);
	}
}

h1, h2, h3, h4, h5, h6 {
    clear: both;
	font-family: var(--headingFont);
	color: var(--headingColor);
	line-height:1.2;
}

h1 {	
	font-size: 4.5rem;
	font-size: calc(4.5rem + 5.294vw);
	line-height:1.1;
	@media (--mobile){
		font-size: 7rem;
		font-size: calc(7rem + 1.66vw);
	}
	@media (--large){
		font-size:9rem;
	}
	margin:1rem 0;
}

h1.entry-title {
	margin:0;
	padding:2rem 4rem;
	color: #fff;
	text-shadow: #ccc -3px -3px 6px, 2px 3px 10px #fff;	
}

h2 {
	font-size: calc(var(--fontSize) * 2.5);
}

h3 {
	font-size: calc(var(--fontSize) * 2);
}

h4 {
	font-size: calc(var(--fontSize) * 1.5);
}

h5,h6 {
	font-size: var(--fontSize);
}

p {
	/* font-family: 'Work+Sans'; */
	
	margin-bottom: 1.5rem;
		font-weight: 300;
}



dfn, cite, em, i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", Courier, monospace;
	font-size: 15px;
	font-size: 1.5rem;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code, kbd, tt, var {
	font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	font-size: 15px;
	font-size: 1.5rem;
}

abbr, acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark, ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}


/* Links - boja i font */

:--link, a:hover {
    color: var(--linkColor);
    text-decoration:none;
    font-family: var(--linkFont);
	font-weight: 300;
	transition: all 0.3s linear;	   
}

a:hover {
	color: var(--linkColorHover);
}


