.alignleft {
	display: inline;
	float: left;
	margin-right: 2rem;
	margin-bottom: 1.25em;
}

.alignright {
	display: inline;
	float: right;
	margin-left: 2rem;
	margin-bottom: 1.25rem;
}

.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 1.25rem;
}

.alignfull {
	margin-top: 5rem;
	margin-right: auto;
	margin-bottom: 5rem;
	margin-left: auto;
	max-width: 100vw;
	position: relative;
	width: 100%;
  }

  .alignwide {
	margin-top: 5rem;
	margin-right: auto;
	margin-bottom: 5rem;
	margin-left: auto;
	max-width: var(--maxSirina);
	position: relative;
	width: 100%;
  }


  .is-style-xl, .xl {
	  max-width: var(--maxSirina);
  }

